import * as d3 from 'd3';

const colors = {
  '': [255, 255, 255], // :0 white
  HSS: [255, 255, 255], // :0 white
  CAMP: [255, 255, 255], // :0 white
  service: 'red', // :1 red
  provider: 'purple', // 2: purple
  patient: 'green', // 5: green
  labs: 'gray', //3 : dark grey
  visit: 'teal', // :4 teal
  diagnoses: 'gold', // 6: gold
};

let data = require('./sampleData.json');

data = data.map(obj => ({ ...obj, color: colors[obj.type] }));

var dataMap = data.reduce(function (map, node) {
  map[node.id] = node;
  return map;
}, {});

var treeData = [];
data.forEach(function (node) {
  var parent = dataMap[node.parent];
  if (parent) {
    (parent.children || (parent.children = []))
      .push(node);
  } else {
    treeData.push(node);
  }
});

const treeMap = { id: "HSS Camp", color: 'white', children: treeData };
console.log(treeMap);
function RadialTree(data, {
  path,
  id = Array.isArray(data) ? d => d.id : null,
  parentId = Array.isArray(data) ? d => d.parentId : null,
  children,
  tree = d3.tree,
  separation = tree === d3.tree ? (a, b) => (a.parent == b.parent ? 1 : 2) / a.depth : (a, b) => a.parent == b.parent ? 1 : 2,
  sort,
  label,
  width = 640,
  height = 400,
  margin = 60,
  marginTop = margin,
  marginRight = margin,
  marginBottom = margin,
  marginLeft = margin,
  radius = Math.min(width - marginLeft - marginRight, height - marginTop - marginBottom) / 2,
  r = 3,
  strokeWidth = 1.5,
  strokeOpacity = 0.8,
} = {}) {
  const root = path != null ? d3.stratify().path(path)(data)
    : id != null || parentId != null ? d3.stratify().id(id).parentId(parentId)(data)
      : d3.hierarchy(data, children);

  // Sort the nodes.
  if (sort != null) root.sort(sort);

  // Compute labels and titles.
  const descendants = root.descendants();
  const L = label == null ? null : descendants.map(d => label(d.data, d));

  // Compute the layout.
  tree().size([2 * Math.PI, radius]).separation(separation)(root);

  const svg = d3.select("#vis").append("svg")
    .attr("viewBox", [-marginLeft - radius, -marginTop - radius, width, height])
    .attr("width", 775)
    .attr("height", 755)
    .attr("style", "max-width: 100%; height: auto; height: intrinsic; background: black;")
    .attr("font-family", "sans-serif")
    .attr("font-size", 10);

  svg.append("g")
    .attr('fill', 'none')
    .attr("stroke-opacity", strokeOpacity)
    .attr("stroke-width", strokeWidth)
    .selectAll("path")
    .data(root.links())
    .join("path")
    .attr("d", d3.linkRadial()
      .angle(d => d.x)
      .radius(d => d.y))
    .attr("stroke", d => d.target.data.color);

  const node = svg.append("g")
    .selectAll("a")
    .data(root.descendants())
    .join("a")
    .attr("transform", d => `rotate(${d.x * 180 / Math.PI - 90}) translate(${d.y},0)`);

  node.append("circle")
    .attr("fill", d => d.data.color)
    .attr("r", r);

  return svg.node();
}

var w = window,
  d = document,
  e = d.documentElement,
  g = d.getElementsByTagName('body')[0],
  x = w.innerWidth || e.clientWidth || g.clientWidth,
  y = w.innerHeight || e.clientHeight || g.clientHeight;

function Tree(data, {
  path,
  id = Array.isArray(data) ? d => d.id : null,
  parentId = Array.isArray(data) ? d => d.parentId : null,
  children,
  tree = d3.tree,
  sort,
  label,
  width = 640,
  height,
  r = 3,
  padding = 1,
  strokeWidth = 1.5,
  strokeOpacity = 1,
  strokeLinejoin,
  strokeLinecap,
} = {}) {

  const root = path != null ? d3.stratify().path(path)(data)
    : id != null || parentId != null ? d3.stratify().id(id).parentId(parentId)(data)
      : d3.hierarchy(data, children);

  if (sort != null) root.sort(sort);

  const descendants = root.descendants();
  const L = label == null ? null : descendants.map(d => label(d.data, d));

  const dx = 10;
  const dy = width / (root.height + padding);
  tree().nodeSize([dx, dy])(root);

  let x0 = Infinity;
  let x1 = -x0;
  root.each(d => {
    if (d.x > x1) x1 = d.x;
    if (d.x < x0) x0 = d.x;
  });

  if (height === undefined) height = x1 - x0 + dx * 2;

  const svg = d3.select("#vis").append("svg")
    .attr("viewBox", [-dy * padding / 2, x0 - dx, width, height])
    .attr("width", 813)
    .attr("height", 813)
    .attr("style", "max-width: 100%; height: auto; height: intrinsic;background: black;")
    .attr("font-family", "sans-serif")
    .attr("font-size", 10);

  svg.attr('transform', function () {
    var me = svg.node();
    var x1 = me.getBBox().x + me.getBBox().width / 2;
    var y1 = me.getBBox().y + me.getBBox().height / 2;
    return `rotate(270, ${x1}, ${y1})`;
  });

  svg.append("g")
    .attr('fill', 'none')
    .attr("stroke-opacity", strokeOpacity)
    .attr("stroke-linecap", strokeLinecap)
    .attr("stroke-linejoin", strokeLinejoin)
    .attr("stroke-width", strokeWidth)
    .selectAll("path")
    .data(root.links())
    .join("path")
    .attr("d", d3.linkHorizontal()
      .x(d => d.y)
      .y(d => d.x))
    .attr("stroke", d => d.target.data.color);

  const node = svg.append("g")
    .selectAll("a")
    .data(root.descendants())
    .join("a")
    .attr("transform", d => `translate(${d.y},${d.x})`);

  node.append("circle")
    .attr("fill", d => d.data.color)
    .attr("r", r);


  return svg.node();
}

Tree(treeMap, {
  width: 16000,
  height: 16000,
  margin: 1000
});

RadialTree(treeMap, {
  width: 4000,
  height: 4000,
  margin: 100
});